//Config for the vuex store
//We are using the vuex plugin pathify to simplify mutations in the nested
//object requiredParam (the state of orders, depots and routes) as well as the 
//nested object optionalParam (state of params like order pairs and default date)
import Vue from 'vue'
import Vuex from 'vuex'
import pathify from 'vuex-pathify'
import {make} from 'vuex-pathify'
import esriLoader from 'esri-loader'


Vue.use(Vuex)

//create the state object
const state = {
  requiredParam: require('@/assets/requiredParameters.json'),
  optionalParam: require('@/assets/optionalParameters.json'),
  jobId: "",
  jobHistory: {},
  jobStatus: "",
  messages: [],
  geocodeReq: "",
  geocodeModal: false,
  showOrderPair: false,
  portal: null
}

//automatically create mutations for nested elements using pathify
const mutations = make.mutations(state)

state.esriModules = {
  esriId: esriLoader.loadModules(["esri/identity/IdentityManager"]),
  esriRequest: esriLoader.loadModules(["esri/request"]),
  Map: esriLoader.loadModules(["esri/Map"]),
  MapView: esriLoader.loadModules(["esri/views/MapView"]),
  Graphic: esriLoader.loadModules(["esri/Graphic"]),
  Geoprocessor: esriLoader.loadModules(["esri/tasks/Geoprocessor"]),
  FeatureLayer: esriLoader.loadModules(["esri/layers/FeatureLayer"]),
  Field: esriLoader.loadModules(["esri/layers/support/Field"]),
  LayerList: esriLoader.loadModules(["esri/widgets/LayerList"]),
  Portal: esriLoader.loadModules(["esri/portal/Portal"]),
  MapImageLayer: esriLoader.loadModules(["esri/layers/MapImageLayer"]),
  Projection: esriLoader.loadModules(["esri/geometry/projection"]),
  Point: esriLoader.loadModules(["esri/geometry/Point"]),
  Search: esriLoader.loadModules(["esri/widgets/Search"])
}

export default new Vuex.Store({
  plugins: [pathify.plugin],
  state: state,
  mutations: mutations,
  getters: {
  //Getters to determine how many orders, depots or routes there are
    orderLen: (state)=>{
      return Object.keys(state.requiredParam.Order).length
    },
  
    depotLen: (state)=>{
      return Object.keys(state.requiredParam.Depot).length
    },
  
    routeLen: (state)=>{
      return Object.keys(state.requiredParam.Route).length
    },
  
  },
  actions: {
  //this action is dispatched when a task is submitted
  //Wait for the response and update messages and job status accordingly 
  //NOTE: jobs can be returned from the server with status success even though
  //the job has failed. We need to check if a succeeded job has failed
  //by looking for 030088 or 030092 in the messages
    ProcessJob({commit, dispatch, state}, promise) {
      commit('SET_JOB_STATUS', 'processing')
      promise.then((response)=>{
        var wasError = response.messages.filter((elem)=>{
          var messages = state.messages;
          messages.push(elem.description)
          commit('SET_MESSAGES', messages)
          return (elem.description.includes('030088') || elem.description.includes('030092'))
        });
        if (response.jobStatus == 'job-succeeded' && wasError.length == 0) {
          commit('SET_JOB_STATUS', 'success')
          commit('SET_JOB_ID', response.jobId)
          dispatch('ParseHistory', response.jobId)
        } else if (response.jobStatus == 'job-failed' || wasError.length > 0) {
          commit('SET_JOB_STATUS', 'failed')
        }
      })
    },
    //Updates the status of the job history dropdown
    //jobId can either be a string returned from a completed task or null
    //null just refreshes the dropdown and makes sure all jobs are within past 24h
    ParseHistory({commit, state}, jobId) {
      var jobhistory = localStorage.getItem('jobhistory');
      if (jobhistory) {
        let newjobhistory = JSON.parse(jobhistory);
        if (jobId && newjobhistory) newjobhistory[jobId] = {date: Date(), requiredParam:state.requiredParam, optionalParam:state.optionalParam};
        for (var key in newjobhistory) {
          if ((new Date().getTime() - new Date(newjobhistory[key]["date"]).getTime()) >= 86400000) delete newjobhistory[key];
        }
        jobhistory = newjobhistory;
      } else if (jobId) {
        jobhistory = {[jobId]: {date: Date(), requiredParam:state.requiredParam, optionalParam:state.optionalParam}};
      } else {
        jobhistory = {};
      }
      localStorage.setItem('jobhistory', JSON.stringify(jobhistory));
      commit('SET_JOB_HISTORY', jobhistory);
    },
    //dispatched when the user clicks a job from the dropdown
    newJob({commit}, {job,info}) {
      commit('SET_JOB_ID', job);
      commit('SET_REQUIRED_PARAM', JSON.parse(JSON.stringify(info.requiredParam)));
      commit('SET_OPTIONAL_PARAM', JSON.parse(JSON.stringify(info.optionalParam)));
    },
    //resets params to deafult settings
    setClear({commit}) {
      commit('SET_REQUIRED_PARAM', require('@/assets/requiredParameters.json'))
      commit('SET_OPTIONAL_PARAM', require('@/assets/optionalParameters.json'))
    },
    //login to arcgis online
    createPortal({commit,state}) {
      const id = localStorage.getItem('esriId');
      state.esriModules.esriId.then(([esriId])=>{
        if (id) {esriId.initialize(JSON.parse(id))}
        state.esriModules.Portal.then(([Portal])=>{
          const portal = new Portal({
            authMode: 'immediate'
          })
          portal.load().then(()=>{
            localStorage.setItem('esriId', JSON.stringify(esriId.toJSON()))
            commit('SET_PORTAL', portal)
          })
        })
      })
    }
  }
})


