<template> 
  <div class="alertholder">
    <b-alert :show="jobStatus==='processing'">
      Job is Processing<Spinner size="medium"/>
    </b-alert>
    <b-alert :show="jobStatus==='success'" variant="success" dismissible>Job Complete</b-alert>
    <b-alert :show="jobStatus==='failed'" variant="danger" dismissible>Job Failure</b-alert>
    <b-alert show variant="warning" dismissible v-for="(message, index) in messages" :key="message" @dismissed="messages.pop(index)">{{message}}</b-alert>
  </div>
</template>
<script>

import Spinner from 'vue-simple-spinner'
import {sync} from 'vuex-pathify'

export default {
  components: {
    Spinner
  },
  computed: {
    jobStatus: sync('jobStatus'),
    messages: sync('messages')
  }
}
</script>
<style>
.alert {
  z-index: 999 !important;
  margin-left: 
}

.alertholder {
  position: fixed;
  z-index: 999 !important;
  left: 0;
  right: 0;
}
</style>